.gallery-section {
  padding-top: 48px;
  padding-bottom: 40px;

  background-image: linear-gradient(
    180deg,
    var(--background-accent) 46%,
    var(--main-white-color) 0
  );

  @media screen and (min-width: $tablet-min) {
    padding-top: 70px;
    padding-bottom: 60px;

    background-image: linear-gradient(
      180deg,
      var(--background-accent) 44%,
      var(--main-white-color) 0
    );
  }

  @media screen and (min-width: $desktop-min) {
    background-image: linear-gradient(
      180deg,
      var(--background-accent) 42%,
      var(--main-white-color) 0
    );
  }
}

.gallery {
  margin-right: auto;
  margin-left: auto;
  width: fit-content;
  display: grid;
  grid-template-columns: repeat(4, 55px);
  grid-template-rows: repeat(15, 55px);
  grid-gap: 3px;

  @media screen and (min-width: $tablet-min) {
    grid-template-columns: repeat(7, 93px);
    grid-template-rows: repeat(4, 43px);
    grid-gap: 7px;
  }

  @media screen and (min-width: $desktop-min) {
    grid-template-columns: repeat(7, 136px);
    grid-template-rows: repeat(6, 64px);
    grid-gap: 7px;
  }
}

.gallery__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 20px;
  box-shadow: none;

  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1),
    box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover {
    transform: scale(1.04);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  }

  @media screen and (min-width: $tablet-min) {
    border-radius: 15px;
  }

  @media screen and (min-width: $desktop-min) {
    border-radius: 20px;
  }
}

.gallery__item--1 {
  grid-column: 2 / span 2;
  grid-row: 1 / span 2;

  @media screen and (max-width: $mobile-max) {
    transform: translateX(25px);
  }
  @media screen and (min-width: $tablet-min) {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 3;
    grid-row-end: 5;
  }
}

.gallery__item--2 {
  grid-column: 1 / span 2;
  grid-row: 3 / span 2;

  @media screen and (min-width: $tablet-min) {
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 2;
    grid-row-end: 4;
  }
}

.gallery__item--3 {
  grid-column: 3 / span 2;
  grid-row: 3 / span 2;

  @media screen and (min-width: $tablet-min) {
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 4;
    grid-row-end: 6;
  }
}

.gallery__item--4 {
  grid-column: 1 / span 4;
  grid-row: 5 / span 4;

  @media screen and (min-width: $tablet-min) {
    grid-column-start: 3;
    grid-column-end: 5;
    grid-row-start: 1;
    grid-row-end: 5;
  }
}

.gallery__item--5 {
  grid-column: 1 / span 2;
  grid-row: 9 / span 2;

  @media screen and (min-width: $tablet-min) {
    grid-column-start: 3;
    grid-column-end: 4;
    grid-row-start: 5;
    grid-row-end: 7;
  }
}

.gallery__item--6 {
  grid-column: 1 / span 2;
  grid-row: 11 / span 2;

  @media screen and (min-width: $tablet-min) {
    grid-column-start: 5;
    grid-column-end: 6;
    grid-row-start: 2;
    grid-row-end: 4;
    transform: translateY(13px);
  }
}

.gallery__item--7 {
  grid-column: 3 / span 2;
  grid-row: 10 / span 2;

  @media screen and (min-width: $tablet-min) {
    grid-column-start: 5;
    grid-column-end: 6;
    grid-row-start: 4;
    grid-row-end: 6;
    transform: translateY(13px);
  }
}

.gallery__item--8 {
  grid-column: 1 / span 2;
  grid-row: 13 / span 2;

  @media screen and (min-width: $tablet-min) {
    grid-column-start: 6;
    grid-column-end: 7;
    grid-row-start: 2;
    grid-row-end: 4;
    transform: translateY(-34px);
  }
}

.gallery__item--9 {
  grid-column: 3 / span 2;
  grid-row: 12 / span 2;

  @media screen and (max-width: $mobile-max) {
    transform: translateY(15px);
  }

  @media screen and (min-width: $tablet-min) {
    grid-column-start: 6;
    grid-column-end: 7;
    grid-row-start: 4;
    grid-row-end: 6;

    transform: translateY(-34px);
  }
}

.gallery__item--10 {
  grid-column: 2 / span 2;
  grid-row: 15 / span 2;
  @media screen and (min-width: $tablet-min) {
    grid-column-start: 7;
    grid-column-end: 8;
    grid-row-start: 3;
    grid-row-end: 5;
    transform: translateY(1px);
  }
}
